.wrapper{
    max-width: 1920px;
    margin: 0 auto;
}

body{
    margin: 0;
    padding: 0;
    background: #F9F8F8;
    // transform: scale(0.8);
    // margin-top: -605px
}

@mixin Roboto($size){
    font-family: 'Roboto' !important;
    font-weight: $size !important;
}

button{
    padding: 0;
}

*{
    box-sizing: border-box !important;
}

.swiper-button-next,.swiper-button-prev{
    cursor: pointer;

    &::after{
        display: none;
    }
}

.index{
    padding-top: 130px;    

    .wrapper{
        display: grid;
        grid-template-columns: 267px 1fr;
        column-gap: 50px;

        aside{
            padding: 0 0 236px 0;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius:0 0 20px 0;
            height: fit-content;

            .aside-list{
                padding: 0 0 0 25px;

                li{
                    padding: 25px 0;
                    border-bottom: 1px solid #C4C4C4;

                    a{
                        display: flex;
                        align-items: center;
                        transition: color .3;

                        &:hover{
                            color: #02569C;
                        }
                    }
                    

                    img{
                        margin-right: 15px;
                    }

                    p{
                        font-size: 20px;
                        line-height: 23px;
                        @include Roboto(400);
                    }

                    &:last-child{
                        padding-bottom: 55px;
                        border: 0;
                    }
                }

                .profile-item{

                    img{
                        border: 1px solid #02569C;
                        width: 30px;
                        height: 30px;
                        object-fit: cover;
                        border-radius: 50%;
                    }

                    p{
                        color: #02569C;  
                    }
                }

                
            }

            .aside-info{
                padding: 30px 13px 20px 13px;
                margin: 0 15px;
                border-top: 1px solid #C4C4C4;
                border-bottom: 1px solid #C4C4C4;

                a,p{
                    font-size: 18px;
                    line-height: 21px;
                    @include Roboto(400);
                    color: #939597;
                    margin-bottom: 10px;
                    display: inline-block;
                }

                a{
                    padding-right: 15px;
                    position: relative;

                    &::after{
                        position: absolute;
                        content: url(../img/svg/dot.svg);
                        right: 1px;
                        top: 50%;
                        transform: translateY(-64%);
                    }
                }
            }

            .aside-adv{
                padding: 30px 15px 0 15px;

                .title{
                    font-size: 18px;
                    line-height: 21px;
                    @include Roboto(400);
                    color: #939597; 
                    margin-bottom: 20px;
                    padding: 0 10px;
                }

                .adv-blk{
                    padding: 0 10px 20px 10px;
                    margin-bottom: 20px;
                    border-bottom: 1px solid #C4C4C4;

                    img{
                        width: 100%;
                        height: 132px;
                        object-fit: cover;
                        margin-bottom: 15px;
                    }

                    p{
                        font-size: 18px;
                        line-height: 21px;
                        @include Roboto(400);
                    }

                    .blk-header{
                        margin-bottom: 10px;
                        color: #0078C1;
                    }

                    &:last-child{
                        border: 0;
                        padding-bottom: 0;
                    }
                }
            }
        }

        main{
            position: relative;
            padding-top: 245px;
            display: grid;
            grid-template-columns: 435px minmax(600px, 940px) 1fr;
            column-gap: 30px;
            height: fit-content;

            .white-block{
                border-radius: 20px;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                background: white;
            }
            
            .intro{
                position: absolute;
                width: 100%;
                object-fit: cover;
                border-radius: 0 0 0 20px;
                top: 0;
                left: 0;
                height: 439px;
            }

            .left{
                position: relative;
                z-index: 1;
                display: flex;
                flex-direction: column;
                height: fit-content;

                .user-photo{
                    width: fit-content;
                    align-self: center;
                    margin-bottom: 25px;

                    img{
                        max-width: 335px;
                        width: 100%;
                        aspect-ratio: 1/1;
                        border: 5px solid #FFFFFF;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    
                }

                .user-is-online{
                    position: relative;
                    &::after{
                        position: absolute;
                        content: url(../img/svg/profile_left_online.svg);
                        top: 15px;
                        right: 40px;
                    }
                }
                
                .subscribe-blk{
                    margin-bottom: 20px;

                    input{
                        display: none;
                    }

                    .subscribe{
                        padding: 22px 0;
                        background: #02569C;
                        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                        border-radius: 20px;
                        @include Roboto(400);
                        text-align: center;
                        color: white;
                        cursor: pointer;
                        transition: background-color .3s;
                        font-size: 22px;

                        &:hover{
                            background-color: #2579BF;
                        }
                    }

                    .subscribed{
                        position: relative;
                        display: none;

                        &>p{
                            padding:19px 0;
                            text-align: center;
                            background: #159700;
                            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                            border-radius: 20px;
                            @include Roboto(400);
                            color: white;
                            cursor: pointer;
                            z-index: 2;
                            position: relative;
                            font-size: 22px;
                        }

                        .subscibed-menu{
                            background: white;
                            width: 100%;
                            padding-top: 30px;
                            border: 1px solid #02569C;
                            border-radius: 20px;
                            margin-top: -31px;
                            z-index: -1;
                            position: absolute;
                            left: 0;
                            top: 100%;
                            visibility: hidden;
                            opacity: 0;
                            pointer-events: none;
                            transition: .3s;
                            z-index: 1;

                            p{
                                font-size: 22px;
                                line-height: 26px;
                                @include Roboto(400);
                                color: #3F3F3F;
                                padding: 15px 35px;
                                transition:  color .3s;
                                border-bottom: 1px solid #3F3F3F;
                                cursor: pointer;
                                

                                &:hover{
                                    color: #0078C1;
                                }

                                &:last-child{
                                    border-bottom: 0;
                                }
                            }
                        }

                        .subscribed-menu-active{
                            visibility: visible;
                            opacity: 1;
                            pointer-events: all;
                        }

                        
                    }

                    
                }

                .write-a-message{
                    border: 1px solid #02569C;
                    box-sizing: border-box;
                    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.1));
                    border-radius: 20px; 
                    padding: 16px;
                    font-size: 22px;
                    line-height: 26px;
                    @include Roboto(400);
                    text-align: center;
                    transition: .3s;
                    color: #02569C;
                    cursor: pointer;
                    margin-bottom: 20px;

                    &:hover{
                        color: white;
                        background: #02569C;
                    }
                }

                .general{
                    padding: 15px 28px 20px 28px;
                    margin-bottom: 20px;
                    
                    .general-header{
                        font-size: 22px;
                        line-height: 26px;
                        @include Roboto(500);
                        text-align: right;
                        color: #001938;
                        margin-bottom: 30px;
                        text-align: center;
                    }

                    .general-main{
                        margin-bottom: 15px;

                        .info-blk{
                            display: grid;
                            align-items: center;
                            grid-template-columns: 1fr 1fr;
                            column-gap: 66px;
                            margin-bottom: 25px;

                            p{
                                font-size: 20px;
                                line-height: 23px;
                                @include Roboto(400);

                            }

                            &>p{
                                padding-left: 14px;
                            }

                            .left{
                                display: flex;
                                align-items: center;
                                flex-direction: row;

                                p{
                                    color: #02569C;
                                    letter-spacing: 0.001em;
                                }
                                
                                img{
                                    width: 26px;
                                    height: 26px;
                                    margin-right: 9px;
                                }
                            }

                            &:last-child{
                                margin-bottom: 0;
                            }
                        }
                    }
                }

                .show-full{
                    font-size: 16px;
                    line-height: 19px;
                    @include Roboto(400);
                    text-align: center;
                    color: #3F3F3F;
                    width: 100%;
                    display: block;
                    transition: .3s;

                    &:hover{
                        color: #02569C;
                    }
                }

                .awards{
                    margin-bottom: 20px;
                    padding: 15px 17px;

                    .left-block-header{
                        margin-bottom: 15px;
                    }

                    .main{
                        display: flex;
                        justify-content: space-between;
                        margin-bottom: 15px;

                        a{
                            border-radius: 50%;

                            img{
                                object-fit: cover;
                            }
                        }
                    }
                }

                .left-block-header{
                    font-size: 22px;
                    line-height: 26px;
                    @include Roboto(400);
                    text-align: right;
                    color: #001938;
                    text-align: center;

                    span{
                        img{
                            transform: translate(2px, -1px);
                        }
                        
                    }
                }

                .friends{
                    padding: 15px 56px 15px 54px;
                    margin-bottom: 20px;

                    h2{
                        margin-bottom: 20px; 
                    }

                    .main{
                        display: grid;
                        grid-template-columns: repeat(3,95px);
                        column-gap: 22px;
                        row-gap: 20px;
                        margin-bottom: 14px;

                        a{
                            display: flex;
                            flex-direction: column;
                            align-items: center;

                            img{
                                width: 75px;
                                height: 75px;
                                border-radius: 50%;
                                object-fit: cover;
                                margin-bottom: 15px;
                            }

                            p{
                                font-size: 18px;
                                line-height: 21px;
                                @include Roboto(400);
                                text-align: center;
                                word-wrap: break-word;
                                color: #02569C;
                                width: 100%;
                                transition: .3s;
                            }

                            &:hover{
                                p{
                                    opacity: 0.8;
                                }
                            }
                        }
                    }
                }

                .subscription{
                    margin-bottom: 20px;
                    padding: 15px 68px 19px 68px;

                    h2{
                        margin-bottom: 18px;
                    }

                    .main{
                        display: flex;
                        flex-direction: column;

                        a{
                            display: grid;
                            grid-template-columns: min-content 1fr;
                            column-gap: 40px;
                            margin-bottom: 20px;

                            img{
                                width: 75px;
                                height: 75px;
                                border-radius: 50%;
                                object-fit: cover;
                                grid-column: 1/2;
                                grid-row: 1/3;
                            }

                            .name{
                                font-size: 22px;
                                line-height: 26px;
                                @include Roboto(400);
                                color: #02569C;
                                align-self: end;
                                width: 100%;
                                word-wrap: break-word;
                                transition: opacity .3s;
                            }

                            .description{
                                font-size: 16px;
                                line-height: 19px;
                                @include Roboto(400);
                                color: #939597;
                                align-self: start;
                                width: 100%;
                                word-wrap: break-word;
                            }

                            &:hover{
                                .name{
                                    opacity: 0.8;
                                }
                            }
                        }
                    }
                }

                .video{
                    padding: 15px 40px 20px 40px;

                    h2{
                        margin-bottom: 20px;
                    }

                    .main{
                        display: flex;
                        flex-direction: column;
                        margin-bottom: 20px;

                        .video-blk{
                            width: 100%;
                            height: 220px;
                            border-radius: 20px;
                            margin-bottom: 15px;
                            position: relative;

                            &:last-child{
                                margin-bottom: 0;
                            }

                            .play{
                                transition: .3s;

                                img{
                                    width: 70px;
                                    height: 70px;
                                }
                                
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                                z-index: 2; 
                            }

                            &:hover{

                                .play{
                                    opacity: 0.8;
                                }
                                
                            }
                        }
                    }
                }
            }

            .right{
                position: relative;
                z-index: 2;
                padding-top: 89px;
                height: fit-content;

                .swiper-button-disabled{
                    display: none;
                }

                .swiper-button-prev{
                    transform: scale(-1);
                    left: 5px;
                }

                .swiper-button-next{
                    right: 5px;
                }

                .user-info{
                    padding: 15px 29px 45px 36px;
                    display: flex;
                    flex-direction: column;
                    margin-bottom: 30px;

                    .user-name{
                        font-size: 30px;
                        line-height: 35px;
                        @include Roboto(400);
                        margin-bottom: 20px;
                        
                        span{
                            margin-left: 13px;
                            display: inline-block;
                            transform: translateY(-5px);
                            cursor: pointer;
                        }
                    }

                    .user-status{
                        font-size: 20px;
                        line-height: 23px;
                        @include Roboto(400);
                        margin-bottom: 25px;

                        span{
                            color: #939597;
                            margin-left: 6px;
                        }
                    }

                    .user-status-info{
                        display: flex;
                        margin-bottom: 31px;

                        .status-info_groupbox{
                            display: grid;
                            grid-template-columns: minmax(min-content, 238px) minmax(min-content, 209px);
                            border-right: 1px solid #02569C;
                            min-height: 37px;
                            align-items: center;
                            padding-top: 5px;

                            p{
                                font-size: 20px;
                                line-height: 23px;
                                @include Roboto(400);

                                span{
                                    margin-right: 10px;
                                    display: inline-block;
                                    transform: translateY(-1px);
                                }
                            }

                            .title{
                                color: #02569C;
                                margin-right: 55px;
                                padding-right: 10px;
                                width: 100%;
                            }

                            &:last-child{
                                grid-template-columns: minmax(min-content, 240px) minmax(min-content, 168px);
                                border-right: 0;

                                .title{
                                    padding-right: 0;
                                    text-align: end;
                                }

                                &>p{
                                    text-align: end;
                                    padding-left: 10px;
                                }
                            }
                        }
                    }

                    .raiting-bar{
                        width: 100%;
                        height: 75px;
                        background: #2579BF;
                        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                        border-radius: 20px;
                        margin-bottom: 30px;

                        .inner-bar{
                            background: #02569C;
                            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                            border-radius: 20px;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: center;
                            min-width: fit-content;
                            width: 74.28%;
                            height: 100%;

                            p{
                                color: #FFFFFF;
                                @include Roboto(400);

                                &:first-child{
                                    font-size: 28px;
                                    line-height: 33px;
                                    margin-bottom: 4px;
                                }

                                &:last-child{
                                    font-size: 17px;
                                    line-height: 20px;
                                }
                            }
                        }
                    }

                    .info-bar{
                        padding: 10px 25px 11px 26px;
                        position: relative;

                        .swiper-slide{
                            width: fit-content;
                            display: flex;
                            flex-direction: column;
                            align-items: center;

                            p{
                                color: #02569C;
                                @include Roboto(400);
                            }

                            .value{
                                font-size: 25px;
                                line-height: 29px;
                                margin-bottom: 5px;
                            }

                            .name{
                                font-size: 17px;
                                line-height: 20px;
                            }
                        }
                    }
                }

                .stories{
                    position: relative;
                    margin-bottom: 30px;

                    .right-title{
                        font-size: 20px;
                        line-height: 23px;
                        margin-bottom: 25px;
                        color: #02569C;
                        margin-bottom: 25px;
                        @include Roboto(400);
                    }

                    .swiper-slide{
                        width: fit-content;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        height: 230px;
                        width: 130px;

                        img{
                            object-fit: cover;
                            width: 100%;
                            height: 100%;
                            border-radius: 10px;
                        }
                    }

                    .swiper-button-next1,.swiper-button-prev1{
                        position: absolute;
                        top: 53%;
                        z-index: 2;
                        height: fit-content;
                        opacity: 0;
                        transition: opacity .3s;
                        cursor: pointer;

                        &:hover{
                            opacity: 1 !important;
                        }
                    }

                    .swiper-button-next1{
                        right: 10px;
                        
                    }

                    .swiper-button-prev1{
                        left: 10px;
                        transform: scale(-1);
                    }

                    &:hover{
                        .swiper-button-next1,.swiper-button-prev1{
                            opacity: 0.5;
                        }
                    }
                }

                .photo{
                    position: relative;
                    margin-bottom: 50px;

                    .right-title{
                        font-size: 20px;
                        line-height: 23px;
                        margin-bottom: 25px;
                        color: #02569C;
                        margin-bottom: 25px;
                        @include Roboto(400);
                    }

                    .swiper-slide{
                        width: fit-content;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        height: 150px;
                        width: 140px;
                        cursor: pointer;

                        img{
                            object-fit: cover;
                            width: 100%;
                            height: 100%;
                            border-radius: 10px;
                        }
                    }

                    .swiper-button-next2,.swiper-button-prev2{
                        position: absolute;
                        top: 53%;
                        z-index: 2;
                        height: fit-content;
                        opacity: 0;
                        transition: opacity .3s;
                        cursor: pointer;

                        &:hover{
                            opacity: 1 !important;
                        }
                    }

                    .swiper-button-next2{
                        right: 10px;
                        
                    }

                    .swiper-button-prev2{
                        left: 10px;
                        transform: scale(-1);
                    }

                    &:hover{
                        .swiper-button-next2,.swiper-button-prev2{
                            opacity: 0.5;
                        }
                    }
                }

                .write-new{
                    padding: 24px 24px 30px 45px;
                    display: flex;
                    gap: 15px;
                    margin-bottom: 106px;
                    box-shadow: unset;


                    &>img{
                        width: 76px;
                        height: 76px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .write-main{
                        width: 100%;
                        display: flex;
                        margin-top: 11px;

                        textarea{
                            width: 100%;
                            background: 0;
                            border: 0;
                            padding: 0;
                            padding: 15px 0;
                            height: fit-content;
                            font-size: 22px;
                            line-height: 26px;
                            @include Roboto(400);
                            color: #3F3F3F;
                            border-bottom: 1px solid #3F3F3F;

                            &::placeholder{
                                opacity: 1;
                                color: #3F3F3F;
                            }
                        }

                        .publish{
                            background: #02569C;
                            border-radius: 5px;
                            font-size: 22px;
                            line-height: 26px;
                            @include Roboto(400);
                            text-align: center;
                            color: #FFFFFF;
                            padding: 13px 48px;
                            display: none;
                            
                        }

                        .button-group{
                            display: flex;
                            margin-left: -249px;
                            margin-top: -12px;
                            
                            button{
                                margin-left: 43px;
                                transition: opacity .3s;

                                &:first-child{
                                    margin-left: 0;
                                }

                                &:hover{
                                    opacity: 0.8;
                                }
                            }
                        }

                        
                    }

                    &.input-active{
                        padding-bottom: 18px;
                        margin-bottom: 50px;

                        .write-main{
                            flex-wrap: wrap;
                        }

                        .button-group{
                            display: flex;
                            margin-left: 0;
                            width: 100%;
                            margin-top: 24px;
                            
                            button{
                                margin-left: 55px;

                                &:first-child{
                                    margin-left: 0;
                                }
                            }

                            .publish{
                                
                                display: block;
                                margin-left: auto;
                            }
                        }
                    }

                    
                }

                .post{
                    padding: 30px 30px 25px 30px;
                    margin-bottom: 30px;

                    .post_header{
                        display: grid;
                        grid-template-columns: min-content 1fr min-content min-content;
                        grid-template-rows: min-content min-content;
                        column-gap: 20px;
                        margin-bottom: 20px;

                        .post-icon{
                            width: 76px;
                            height: 76px;
                            position: relative;
                            grid-column: 1/2;
                            grid-row: 1/3;
                            
                            img{
                                width: 100%;
                                height: 100%;
                                border-radius: 50%;
                                object-fit: cover;
                            }

                            &.online{
                                &::after{
                                    content: "";
                                    background: url("../img/svg/profile_left_online.svg") center;
                                    background-size: cover;
                                    position: absolute;
                                    width: 20px;
                                    height: 20px;
                                    top: 0;
                                    right: 6px;
                                }
                            }
                        }

                        .name{
                            align-self: end;
                            font-size: 25px;
                            line-height: 29px;
                            @include Roboto(400);
                            grid-column: 2/3;
                            grid-row: 1/2;

                            span{
                                margin-left: 6px;
                                display: inline-block;
                                transform: translateY(-2px);
                            }
                        }

                        .date{
                            align-self: start;
                            font-size: 18px;
                            line-height: 21px;
                            color: #3F3F3F;
                            @include Roboto(400);
                            grid-column: 2/3;
                            grid-row: 2/3;
                            margin-top: 3px;
                        }

                        .watch-amount{
                            grid-column: 3/4;
                            grid-row: 1/3;
                            align-self: center;
                            display: flex;
                            flex-wrap: nowrap;
                            align-items: center;
                            font-size: 17px;
                            line-height: 20px;
                            @include Roboto(400);
                            color: #3F3F3F;
                            margin-right: 21px;
                            
                            span{
                                display: inline-block;
                                margin-right: 10px;
                            }
                        }

                        
                    }

                    .post_content{
                        width: 100%;
                        height: 480px;
                        margin-bottom: 30px;
                        cursor: pointer;
                        
                        img{
                            object-fit: cover;
                            width: 100%;
                            height: 100%;
                        }
                    }

                    .several-photo{
                        display: grid;
                        grid-template-columns: 1fr 264px;
                        grid-template-rows: 1fr 1fr;
                        gap: 14px;

                        img:nth-child(1){
                            grid-column: 1/2;
                            grid-row: 1/3;
                        }

                        img:nth-child(2){
                            grid-column: 2/3;
                            grid-row: 1/2;
                        }

                        img:nth-child(3){
                            grid-column: 2/3;
                            grid-row: 2/3;
                        }
                    }

                    .video{
                        video-js{
                            width: 100%;
                            height: 100%;

                            .vjs-control-bar{
                                background: none;
                                margin-bottom: 5px;
                            }

                            .vjs-big-play-button{
                                background: none;
                                border: none;
                            }

                            .vjs-big-play-button .vjs-icon-placeholder:before {
                                content: url(../img/svg/video-play-btn.svg);
                                transform: scale(1.25) translateY(-13px);
                            }

                            .vjs-play-control .vjs-icon-placeholder:before {
                                
                                transform: scale(1.5);
                            }

                            .vjs-mute-control .vjs-icon-placeholder:before {
                                
                                transform: scale(1.3);
                            }

                            .vjs-fullscreen-control .vjs-icon-placeholder:before {
                                
                                transform: scale(1.5);
                            }

                            .vjs-picture-in-picture-control .vjs-icon-placeholder:before {
                                
                                transform: scale(1.2);
                            }

                            .vjs-remaining-time-display{
                                font-size: 15px;
                            }
                                
                            
                        }
                    }

                    .single-photo{
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }

                    .post_text{
                        margin-bottom: 26px;

                        p{
                            font-size: 22px;
                            line-height: 26px;
                            @include Roboto(400);
                            color: #3F3F3F;
                        }
                    }

                    .post_comments{

                        .comment-blk{
                            border-bottom: 1px solid #C4C4C4;

                            
                            .sub-comment-blk{
                                padding-left: 27px;
                                
                                .comment{
                                    padding-top: 0;
                                }

                                
                            }

                            .show-answers{
                                font-size: 17px;
                                line-height: 20px;
                                @include Roboto(400);
                                color: #939597;
                                margin-bottom: 15px;
                                display: block;
                                transition: color .3s;

                                &:hover{
                                    color: #02569C;
                                }
                            }
                        }

                        .comment-input{
                            display: flex;
                            align-items: center;

                            .input-group{
                                border: 1px solid #939597;
                                border-radius: 5px;
                                width: 100%;
                                display: flex;
                                flex-wrap: nowrap;
                                align-items: start;

                                .button-group{
                                    display: flex;
                                    align-items: center;
                                    margin-top: 12px;
                                
                                    button{
                                        height: 26px;
                                        margin-right: 15px;

                                        svg{
                                            path{
                                                stroke: black;
                                                transition: color .3s;
                                            }
                                            

                                            &:hover{
                                                path{
                                                    stroke: #02569C;
                                                }
                                                
                                            }
                                        }
                                    }

                                    .send-message{
                                        font-size: 17px;
                                        line-height: 20px;
                                        @include Roboto(400);
                                        text-align: right;
                                        color: #02569C;
                                        transition: .3s;

                                        &:hover{
                                            opacity: 0.8;
                                        }
                                    }
                                }
                            }

                            .user-thumb{
                                width: 50px;
                                height: 50px;
                                margin-right: 13px;
                                object-fit: cover;
                                border-radius: 50%;
                            }

                            .textarea{
                                width: 100%;
                                resize: none;
                                background: 0;
                                border: 0;
                                padding: 15px 12px;
                                font-size: 17px;
                                line-height: 20px;
                                @include Roboto(400);
                                word-break: break-word;

                                &>.placeholder {
                                    color: #939597;
                                    pointer-events: none;
                                }

                                &>.mention{
                                    color: #02569C;
                                }

                                & .text{
                                    color: white;
                                }
                            }
                        }

                        .main-comment-show-all{
                            margin: 0 auto;
                            margin-top: 15px;
                            margin-bottom: 11px;
                            display: block;
                            font-size: 17px;
                            line-height: 20px;
                            @include Roboto(400);
                            text-align: center;
                            color: #3F3F3F;
                            transition: .3s;

                            &:hover{
                                color: #02569C;
                            }
                        }

                        .main-comment-input{
                            padding-top: 15px;
                        }

                        .sub-comment-blk{
                            .sub-comment-input{
                                .comment-input{
                                    display: none;
                                }
                                .comment-input.active{
                                    display: flex;
                                }
                            }

                        }
                    }
                }
            }

            .bottom{
                width: 100%;
                height: 200px;
                background: red;
                grid-column: 1/3;
            }

        }
    }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------=======POST FOOTER======------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.post_footer{
    display: flex;
    position: relative;

    .raiting-block{
        display: grid;
        grid-template-columns: min-content 1fr min-content;
        margin-left: 1px;
        position: relative;
        
        .up{
            align-self: center;
            margin-right: 14px; 
            display: inline-block;
            transform: translateY(-1px);   
            cursor: pointer;

            &.active{
                fill: #159700;
            }
        }

        .down{
            align-self: center;
            margin-left: 11px; 
            display: inline-block;
            transform: translateY(2px);
            cursor: pointer;

            &.active{
                fill: red;
            }
        }

        .raiting{
            height: 46px;
            width: 46px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #3F3F3F;
            border-radius: 50%;
            font-size: 17px;
            line-height: 20px;
            @include Roboto(400);
            color: #3F3F3F;
        }

        .raiting-text{
            grid-column: 1/4;
            grid-row: 2/3;
            justify-self: center;
            font-size: 17px;
            line-height: 20px;
            text-align: center;
            @include Roboto(300);
            margin-top: 5px;                                
            color: #3F3F3F;
            text-align: center;
            cursor: pointer;
            position: relative;

            &::after{
                content: "";
                position: absolute;
                color: red;
                width: 80px;
                height: 80px;
                bottom: 100%;
                left: 10px;
                display: none;
            }

            &:hover{

                &::after{
                    display: block;
                }
            }
        }

        .raiting-popup{
            position: absolute;
            width: 416px;
            padding: 10px 8px;
            z-index: 2;
            bottom: 117%;
            left: -31px;
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transition: .3s;

            .popup-header{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 8px;

                h2{
                    font-size: 18px;
                    line-height: 21px;
                    @include Roboto(400);
                    color: #FFFFFF;
                }

                img{
                    display: none;
                }
            }

            .popup-main{
                display: flex;
                justify-content: space-between;

                .popup-item{
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    img{
                        width: 40px;
                        height: 40px;
                        border: 1px solid #FFFFFF;
                        object-fit: cover;
                        border-radius: 50%;
                        margin-bottom: 5px;
                    }

                    p{
                        font-size: 15px;
                        line-height: 18px;
                        @include Roboto(400);
                        text-align: center;
                        
                        color: #FFFFFF;  
                    }
                }
            }

            &::after{
                position: absolute;
                content: url(../img/svg/raiting-pp-bg.svg);
                left: 0;
                top: 0;
                z-index: -1;
            }
        }

        .raiting-text:hover + .raiting-popup{
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }

        .raiting-popup:hover{
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
    }

    .commetns, .reposts{
        display: flex;
        align-items: center;
        font-size: 17px;
        line-height: 20px;
        color: #3F3F3F;
        @include Roboto(400);

        span{
            margin-right: 5px;
        }
    }

    .commetns, .reposts, .add-to-bookmarks{
        height: fit-content;
        margin-top: 6px;
    }

    .add-to-bookmarks{
        margin-left: 21px;
    }

    .reposts{
        margin-left: 15px;
        cursor: pointer;
    }

    .repost-popup{
        position: absolute;
        padding: 26px 24px;
        display: flex;
        flex-direction: column;
        z-index: 2;
        top: 56%;
        right: 91px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        transition: .3s;

        button{
            font-size: 18px;
            line-height: 21px;
            @include Roboto(400);
            margin-bottom: 14px;
            color: #02569C;
            text-align: left;
            transition:  opacity .3s;

            &:hover{
                opacity: 0.8;
            }
        }

        &::after{
            content: url(../img/svg/repost-cloud.svg);
            position: absolute;
            left: 0;
            top: 0;

            z-index: -1;
        }

        &.active{
            visibility: visible;
            opacity: 1;
            pointer-events: all;
        }
    }

    .commetns{
        
        margin-left: auto;
    }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------=======MORE PART======-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.more{
    grid-column: 4/5;
    grid-row: 1/3;
    align-self: center;
    position: relative;
    z-index: 2;
    cursor: pointer;

    .more-option{
        width: 216px;
        height: 104px;
        position: absolute;
        top: 140%;
        right: 0px;
        display: flex;
        flex-direction: column;
        padding: 26px 24px 22px 24px;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transition: .3s;


        .bg{
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;

        }

        button{
            padding: 0;
            text-align: left;
            font-size: 18px;
            line-height: 21px;
            @include Roboto(400);
            margin-bottom: 14px;
            color: #02569C;
            transition:  opacity .3s;

            &:last-child{
                margin-bottom: 0;
            }

            &:hover{
                opacity: 0.8;
            }
        }

        
    }

    &.more-option-active{

        .more-option{
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
        
    }

    
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------=======COMMETNT======-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.comment{
    padding: 15px 0;
    display: grid;
    grid-template-columns: 1fr min-content;

    .comment_header{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 10px;

        img{
            width: 38px;
            height: 38px;
            object-fit: cover;
            margin-right: 7px;
            border-radius: 50%;
        }

        .name{
            font-size: 17px;
            line-height: 20px;
            @include Roboto(400);
            color: #02569C;

            &.verified{
                position: relative;

                &::after{
                    display: inline-block;
                    transform: translateY(4px) translateX(2px);
                    content: url(../img/svg/commetns-verified.svg);
                }
            }
        }

        
    }

    .comment_text{
        font-size: 17px;
        line-height: 20px;
        @include Roboto(400);
        margin-bottom: 10px;

        & .mention{
            color: #02569C;
        }
    }

    .comment_footer{
        display: flex;

        p,button{
            font-size: 16px;
            line-height: 19px;
            @include Roboto(400);
        }

        button{
            padding: 0;
            text-align: left;
            transition:  opacity .3s;
            color: #02569C;

            &:hover{
                opacity: 0.8;
            }
        }

        .date{
            color: #939597;
            margin-right: 15px;
        }
    }

    .comment_likes{
        grid-column: 2/3;
        grid-row: 1/4;
        align-self: center;
        white-space: nowrap;
        color: #02569C;
        font-size: 17px;
        line-height: 20px;
        @include Roboto(400);

        svg{
            path{
                fill:none;
                transition:.3s;
            }
            
            cursor: pointer;
        }
        
        .likes-amount{
            margin-right: 5px;
        }

        &.like-checked{
            svg{
                path{
                    fill:#02569C;
                }
                
            }
        }

        span:last-child{
            display: inline-block;
            transform: translateY(2px);
        }
    }
}


