/* ------------------------------------ */
/* -------========HEADER========------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

header{
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 6;

    .wrapper{
        padding: 23px 79px 12px 25px;
        display: flex;
        flex-direction: row;

        .header_logo-group{
            margin: 0px 0 11px 0;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 8px;
        }

        .search-bar-group{
            display: flex;
            margin-left: 75px;
            flex-direction: row;
            padding: 10px 0 9px 0;
            max-width: 465px;
            width: 100%;

            .search-bar{
                border: 1px solid #02569C;
                box-sizing: border-box;
                border-radius: 47px;
                background: 0;
                padding: 13px 48px 13px 24px;
                width: 100%;
                @include Roboto(400);
                font-size: 20px;
                line-height: 23px;
                height: fit-content;

                &::placeholder{
                    opacity: 1;
                    color: black;
                }
            }

            .search-btn{
                background: 0;
                border: 0;
                display: block;
                padding: 0;
                margin-left: -39px;
            }

        }

        .nav-group{
            max-width: 605px;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            margin: 13px 0;

            .nav{
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                margin-left: 20px;
                max-width: 416px;
                width: 100%;

                li{

                    a{
                        padding:0 10px;

                        svg path{
                            transition: .3s;                           
                        }

                        .shape,.door, .photo{
                            fill: white;
                        }

                        .dot{
                            fill: #02569C;
                            
                        }

                        .line{
                            stroke: #02569C;
                        }
                        
                        &:hover{
                            .shape{
                                fill: #02569C;
                            }

                            .door, .photo,.dot{
                                fill: white;
                                
                            }

                            .line{
                                stroke: white;
                            }
                        }
                    }

                    &:first-child{
                        a{
                            padding-left: 0;
                        }
                    }

                    &:last-child{
                        a{
                            padding-right: 0;
                        }
                    }
                }
            }
        }

        .header_profile{
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-left: auto;
            position: relative;
            padding-left: 20px;

            .profile-name{
                font-size: 20px;
                line-height: 23px;
                text-align: center;
                @include Roboto(400);
                color: #02569C;
                display: inline-block;
                margin-right: 20px;
                flex-shrink: 0;
            }

            .profile-image{
                width: 70px;
                height: 70px;
                border: 2px solid #02569C;
                border-radius: 50%;
                object-fit: cover;
                margin-right: 20px;
                flex-shrink: 0;
                transform: translateY(-2px);
            }

            .profile-more{
                cursor: pointer;
            }

            .profile_dropdown{
                padding: 20px 20px 24px 20px;
                position: absolute;
                top: 100%;
                right: -78px;
                background: white;
                width: 332px;
                opacity: 0;
                visibility: hidden;
                pointer-events: none;
                transition: .3s;

                .dropdown_header{
                    display: grid;
                    grid-template-columns: min-content 1fr min-content;
                    grid-template-rows: min-content min-content;
                    padding-bottom: 20px;
                    border-bottom: 1px solid #939597;

                    .thumb{
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        border: 2px solid #02569C;
                        object-fit: cover;
                        margin-right: 10px;
                        grid-column: 1/2;
                        grid-row: 1/3;
                    }

                    .name{
                        font-size: 20px;
                        line-height: 23px;
                        text-align: center;
                        @include Roboto(400);
                        color: #02569C;
                        margin-top: 2px;
                        grid-column: 2/3;
                        grid-row: 1/2;
                        justify-self: baseline;
                        align-self: baseline;
                    }

                    .view-profile{
                        font-size: 15px;
                        line-height: 18px;
                        @include Roboto(400);
                        text-align: center;
                        color: #939597; 
                        grid-column: 2/3;
                        grid-row: 2/3;
                        justify-self: baseline;
                        align-self: baseline;
                    }

                    .close-dropdown{
                        align-self: center;
                        justify-self: center;
                        grid-column: 3/4;
                        grid-row: 1/3;
                        cursor: pointer;
                    }

                }
                
                .dropdown_main{
                    padding-top: 20px;

                    li{
                        margin-bottom: 25px;

                        &:last-child{
                            margin-bottom: 0;
                        }
                    }

                    a{
                        display: flex;
                        align-items: center;
                        position: relative;

                        .ico{
                            margin-right: 15px;
                        }

                        .opt-name{
                            font-size: 14px;
                            line-height: 16px;
                            @include Roboto(400);
                            color: #000000;
                            transition:  color .3s;
                        }

                        &::after{
                            content: url(../img/svg/profile_dropdown_option-arrow.svg);
                            position: absolute;
                            width: 10px;
                            height: 19px;
                            top: 50%;
                            right: 0;
                            transform: translateY(-50%);
                            transition: transform .3s;
                        }

                        &:hover{
                            .opt-name{
                                color: #02569C;
                            }

                            &::after{
                                transform: translateY(-50%) translateX(4px);
                            }
                        }
                    }
                }
            }

            .profile_dropdown-active{
                opacity: 1;
                visibility: visible;
                pointer-events: all;
            }
        }
    }


}

