.popup {
    z-index: 10000;
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(3px);
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: .3s;

    .pp-close{
        position: absolute;
        right: 100px;
        top: 45px;
        cursor: pointer;
    }

    .wrapper{
        position: relative;

        .next-post{
            position: absolute;
            left: calc(100% + 20px);
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
        }
    }
}
  
.popup-active {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
}

.photo_popup{

    .wrapper{
        max-width: 1231px;
        background: #FFFFFF;
        border-radius: 15px;
        max-height: 761px;

        display: grid;
        grid-template-columns: 1fr minmax(300px, 471px);
        grid-template-rows: 1fr min-content;

        .photo{
            grid-column: 1/2;
            grid-row: 1/2;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .photo-info{
            grid-column: 2/3;
            grid-row: 1/3;
            display: grid; 
            grid-template-rows: min-content min-content 1fr min-content;
            
            .info-header{
                border-radius: 0 15px 0 0;
                background: #F4F4F4;
                padding: 15px 20px;
                display: grid;
                grid-template-columns: min-content 1fr min-content;
                grid-template-rows: 1fr 1fr;

                .name{
                    align-self: end;
                    font-size: 18px;
                    line-height: 21px;
                    @include Roboto(400);
                    grid-column: 2/3;
                    grid-row: 1/2;

                    span{
                        margin-left: 5px;
                    }
                }

                .date{
                    font-size: 16px;
                    line-height: 19px;
                    @include Roboto(400);
                    grid-column: 2/3;
                    grid-row: 2/3;
                    color: #3F3F3F;
                    align-self: start;
                    margin-top: 3px;
                }

                .header-photo{
                    grid-column: 1/2;
                    grid-row: 1/3;
                    width: 55px;
                    height: 55px;
                    object-fit: cover;
                    margin-right: 15px;
                    justify-self: center;
                    border-radius: 50%;
                }
            }
            
            .info-main{
                padding: 20px 20px 20px 27px;
                border-top: 1px solid #C4C4C4;
                border-bottom: 1px solid #C4C4C4;
                height: fit-content;

                p{
                    font-size: 17px;
                    line-height: 20px;
                    @include Roboto(400);
                    color: #000000;
                }

                .mention{
                    margin-top: 35px;

                    span{
                        color: #02569C;
                    }
                }
            }

            .info-commetns{
                padding: 14px 20px 14px 20px;
                width: 100%;
                height: 100%;
                overflow-y: scroll;

                .comment{
                    padding-top: 0;
                }
            }

            .info-wrire-block{
                display: flex;
                padding: 25px 17px;
                align-content: center;
                border-top: 1px solid #C4C4C4;

                input{
                    border: 1px solid #02569C;
                    border-radius: 47px;
                    background: 0;
                    padding: 10px 41px 10px 15px;
                    font-size: 16px;
                    line-height: 19px;
                    @include Roboto(400);
                    display: block;
                    height: fit-content;
                    align-self: center;

                    &::placeholder{
                        opacity: 1;
                        color: #3F3F3F;
                    }
                }

                .emoji{
                    margin-left: -39px;
                }

                .more{
                    margin-left: 29px;
                }

                .send{
                    margin-left: 14px;
                    font-size: 16px;
                    line-height: 19px;
                    @include Roboto(400);
                    text-align: center;
                    padding: 7px;
                    max-width: 133px;
                    min-width: fit-content;
                    color: #FFFFFF;
                    background: #02569C;
                    border-radius: 5px;
                    width: 100%;
                    transition: opacity .3s;

                    &:hover{
                        opacity: 0.8;
                    }
                }
            }
        }

        .photo-footer{
            grid-column: 1/2;
            grid-row: 2/3;
            background: #F4F4F4;
            padding: 11px 20px 10px 20px;
            border-radius: 0 0 0 15px;


            .post_footer{
                align-items: center;

                .repost-popup{
                    top: unset;
                    bottom: 64%;
                    padding: 17px 24px;


                    &::after{
                        transform: scale(1, -1);
                    }
                }
            }
        }
    }
}
